	module.exports = {"data":{"post":{"html":"<p>We discovered a new walking trail today... a secret path through a cow pasture to a foot bridge connecting Thelstorf and Weihe across the Seeve creek. The 90 minute round is almost all paved, good for pushing the baby carriage! Of course we did not have the camera along. </p>\n<p>Mama is feeding <a href=\"http://www.biobest.be/eng/EHeterorhabditis.htm\">nematodes</a> to her rhododendrons. Or is she feeding the rhodos to the nematodes?</p>\n<p>Today Christopher's cousins Lennart and Mareike bicyled to Lüllau to pay us a visit.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/05/crh0501.jpg\" height=\"300\" width=\"350\" border=\"0\" alt=\"crh0501.jpg: \" /></p>","fields":{"title":"May Day","date":"01 May 2000","url":"https://papascott.de/2000-05-01-may-day/"}}},"pathContext":{"slug":"/2000-05-01-may-day/","prev":{"fields":{"title":"Sunday","date":"30 Apr 2000","slug":"/2000-04-30-sunday/"}},"next":{"fields":{"title":"Eskimo Roll","date":"03 May 2000","slug":"/2000-05-03-eskimo-roll/"}}}}

